body {
  color: #2e2f42;
  background: #f4f4fd;
  margin: 30px 0;
  font-family: Roboto, sans-serif;
}

h2, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.breed-select {
  width: 300px;
  height: 30px;
  text-align: center;
  margin: auto auto 30px;
  display: flex;
}

.breed-select:hover, .breed-select:focus {
  border-color: rgba(68, 107, 157, .15);
  box-shadow: 0 4px 4px rgba(68, 107, 157, .15);
}

.cat-info {
  background-color: #e4e4e7;
  padding: 30px 60px;
}

.header-cat-info {
  text-align: center;
  margin-bottom: 20px;
  font-size: 30px;
}

.section-cat {
  gap: 30px;
  display: flex;
}

.div-cat-right {
  display: block;
}

.text-cat-info-first {
  padding-bottom: 30px;
  font-size: 16px;
}

.text-cat-info-second {
  font-size: 20px;
}

.span {
  font-size: 24px;
  font-weight: 500;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #6ed457;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 0 auto;
  animation: rotation 1s linear infinite;
  display: inline-flex;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #ff3d00;
  border-radius: 50%;
  margin: 0 auto;
  animation: rotation .5s linear infinite reverse;
  display: inline-flex;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

